import React, { useEffect } from 'react'
import $ from 'jquery'
import WhatWeDoList from './WhatWeDoList'
import WWDComponentWrap from './WWDComponentWrap';




const WhatWeDo = (props) =>{
     useEffect(function() {
    var $animation_elements = $('.animate-from-bottom');
    var $window = $(window);

    function check_if_in_view() {
        var window_height = $window.height() / 1.3;
        var window_top_position = $window.scrollTop();
        var window_bottom_position = (window_top_position + window_height);
        $.each($animation_elements, function () {
            var $element = $(this);
            var element_top_position = $element.offset().top;
            if (element_top_position <= window_bottom_position) {
                $element.addClass('in-view');
            } else {}
        });
    }
    $window.on('scroll resize',check_if_in_view);$window.trigger('scroll');

    if($(window).width() < 768){
        $('.what-we-do-wrap div').removeClass('animate-from-bottom')
    }


  });
    return(
        <>
        <section className="what-we-do-wrap">
                <div className="common-wrap clear">
                    <div className="what-we-do-inner animate-from-bottom">
                        <WhatWeDoList/>
                        <WWDComponentWrap/>
                    </div>
                </div>
            </section>
        </>
    )
}

export default WhatWeDo;